@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&family=Tajawal:wght@200;300;400;500;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&family=Tajawal:wght@200;300;400;500;900&display=swap);
body {
  margin: 0;
  font-family: 'Cairo', sans-serif;
}


  * {
    margin: 0;
    padding: 0;
    font-family: 'Cairo', sans-serif;
}
.root{
    border-bottom: 1px solid rgba(180, 180, 180, 0.374);
    height: 70px;
}
.navbar-logo{
    list-style: none;
}
img {
    width: 200px;
    float: right;
    margin-right: 30px;
    margin-top: 10px;
}

nav ul li  {
    text-decoration: none;
    float: left;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 30px;
    list-style: none;
    
    

}
a{
    text-decoration: none;
    color:black;
    font-weight: 700;
}
.signin{
    margin-left: 20px;
    float: left;
    margin-top: 25px;

}
.list{
    float: right;
    margin-right: 200px;
    margin-top: 25px;
}

.signup{
    border:1px solid #73BCA3 ;
    border-radius: 25px;
    width: 100px;
    height: 10px;
    padding: 10px;
}
.signup a {
    color: #73BCA3;
}
.signup:hover,.signup-l:hover {
    background-color:#73BCA3 ;
    color: white;
}
ul li a:hover{
    color: #73BCA3;
}


.header {
    width: 500px;
    direction: rtl;
    float: right;
    margin-right: 40px;
    margin-top: 14%;
    font-family: 'Cairo', sans-serif;

}

h1 {
    color: #4C7598;

}

p {
    color: #73BCA3;
    font-weight: bold;
    font-size: 18px;
}

button {
    width: 150px;
    background-color: #4C7598;
    border: none;
    border-radius: 13px;
    padding: 3px;
    margin-top: 30px;
    color: white;
}

.icon {
    padding-right: 6px;
}
.title {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 110%;
    margin-top: 20%;
    color: #4C7598;
}


.card {
    display: block;
    margin-top: 80px;
    justify-content: space-between;
    margin-right: 90px;

}

.box1 {
    width: 50px;
    height: 50px;
    background-color: #73BCA3;
    box-shadow: -10px 10px #73bca24e;
    border-radius: 5px;
}

.icon1 {
    margin: 15px;
    color: white;
}

h5 {
    color: #4C7598;
    margin-top: 15px;
    font-size: 20px;
    font-weight: bolder;
}

.describe {
    width: 250px;
    font-size: 15px;
    font-weight: 500;
}

.cards {
    display:grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    gap: 20px;
}
.c-title {

    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 90%;
    margin-top: 20%;
    color: #4C7598;
    width: 200px;
}

.type-m{
    margin-right: 50%;
    margin-top: 15px;
    width: 600px;
    height: 40px;
    border: 1px solid #abababbb;
    background-color: white;
    border-radius: 10px;
}
option{
    color: rgb(0, 0, 0);
}
.type {

    width: 600px;
    height: 40px;
    border: 1px solid #abababbb;
    border-radius: 10px;
    padding: 5px;
    background-color: white;
}
.error-message{
    color: red;
    margin-right: 50%;
    font-size: 12px;

}
.alert{
    border: 2px solid red;
}
.message{
    width: 600px;
    height: 300px;
    padding: 0;
    margin-right: 50%;
    margin-top: 15px;
    border: 1px solid #abababbb;
    border-radius: 10px;
}
.send-m{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 92%;
    margin-top: 15px;
    margin-bottom: 15px;
}
